import { useState } from "react";
import { Button, Form, FormControl } from "react-bootstrap";

type addedDataType = {
	date: string;
	way: string;
	kind: string;
	content: string;
	amount: string;
};

const AddedValues = ({ addedData }: { addedData: addedDataType }) => {
    const [formClass, setFormClass] = useState("border rounded-3 p-3 my-3");

	const [kindValue, setKindValue] = useState("");

	const options = ["", "", "", "", "", "", "", "", "", "", ""];
	const optionElems: JSX.IntrinsicElements["option"][] = [];
	options.forEach((option, index) => {
		optionElems.push(
			<option value={option} key={index + 1}>
				{option}
			</option>
		);
	});

	const [contentValue, setContentValue] = useState("");

    const buttonOnClick = () => {
        setFormClass("d-none")
    }

	return (
		<Form className={formClass}>
			<p>
				{addedData.date}{addedData.way}{addedData.amount}
			</p>
			<Form.Select
				className="mb-3"
				value={kindValue}
				onChange={(e) => {
					const currentVal = e.currentTarget.value;
					setKindValue(currentVal);
				}}
			>
				<option disabled value={""}>
					
				</option>
				{optionElems}
			</Form.Select>
			<FormControl
				className="mb-3"
				placeholder={addedData.content}
				value={contentValue}
				onChange={(e) => {
					setContentValue(e.currentTarget.value);
				}}
			/>
			<Button onClick={buttonOnClick}></Button>
		</Form>
	);
};

export default AddedValues;
